
.address {
  height: 380px;
  background: linear-gradient(90deg,  
        #232526,#414345);

  // border-top: 2px solid #000046→#1cb5e0 rgb(1, 53, 4);
  &_box{
    display: flex;
    padding: 22px 32px;
    height: 100%;
    gap: 3%;

    &_cards{
      // border: 3px dashed deeppink;
      flex-grow: 1;
      border-radius: 14px;
      height:100%;
      transition: all .4s;
      background-repeat: no-repeat;
      background-size: cover;
      box-shadow: 3px 0 11px 8px rgba(0, 0, 0, 0.35);
      &:nth-child(1){
        background-image: linear-gradient(90deg,  
        #1cb5e0,#00000073),url(./uk.png);}
      &:nth-child(2){
          background-image: linear-gradient(90deg,  
          #1cb5e0,#1cb5e000),url(./nl.png);}

      &:hover{
        transform: translateY(-1px) scale(1.01);
        transition: all .3s;
      }
    }
  }
  }
